.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.App-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
}

footer {
  background-color: #bb1c74;
  padding: 20px;
  color: white;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  background-color: #bb1c74;
}

h1 {
  text-align: center;
  color: #fff;
}

p {
  margin-bottom: 10px;
  color: white;
}

.p-margin {
  margin-left: 25%;
  margin-right: 25%;
  color: #fff;
}

label {
  display: block;
  margin-bottom: 20px;
  margin-top: 20px;
  font-size: 16px;
}

input[type="text"] {
  width: 200px;
  padding: 5px;
  font-size: 16px;
}

button {
  padding: 5px 10px;
  font-size: 16px;
}

#result {
  margin-top: 3px;
  color: black;
  font-size: 25px;
  padding: 10px;
}

#hiddenLink {
  margin-top: 3px;
  display: none;
  padding: 30px;
  background-color: #bb1c74;
  font-size: 20px;
  color: #fff; /* Update hidden link color */
}

#hiddenLink a {
  display: block;
  color: #fff; /* Update hidden link color */
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #bb1c74;
  padding: 10px;
}

.logo-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.logo {
  width: 300px;
  height: auto;
}

.menu-icon {
  display: none;
  font-size: 20px;
  cursor: pointer;
}

.menu-items {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu-item {
  margin-bottom: 10px;
}

@media (max-width: 768px) {
  .menu-icon {
    display: block;
  }

  .menu-items {
    display: none;
  }

  .menu-items.show {
    display: flex;
  }

  .menu-icon.clicked {
    margin-bottom: 20px;
  }

  .navbar {
    background-color: #bb1c74; /* Update the background color for mobile view */
  }

  .logo-container {
    flex-direction: column;
    justify-content: center;
    margin: 10px 0;
  }

  .logo {
    width: 300px;
    height: auto;
    margin-bottom: 10px;
  }
  
  .menu-item a {
    color: #fff;
  }
  
  .menu-item a:hover,
  .menu-item a:focus {
    color: #fff;
  }
}

.menu-item a {
  color: #fff;
}

.menu-item a:hover,
.menu-item a:focus {
  color: #fff;
}
